header {
  background: #203f15; }
  header .navbar {
    background: none;
    border: none;
    min-height: 40px;
    margin-bottom: 0px; }
  header .navbar-brand {
    padding: 0px 15px;
    width: auto;
    margin-top: 0px;
    height: auto;
    line-height: 100%;
    font-size: 34px; }
    header .navbar-brand img {
      width: 30px;
      height: auto;
      display: inline; }
    header .navbar-brand .brand {
      font-size: 34px;
      font-weight: 800;
      color: #FFFFFF;
      vertical-align: middle; }
  header ul.navbar-nav {
    margin-top: 5px;
    margin-bottom: 0px; }
    header ul.navbar-nav li {
      position: relative;
      border-right: 1px solid #1a1a1a; }
      header ul.navbar-nav li:last-child {
        border-right: none; }
    header ul.navbar-nav li > a {
      color: #FFFFFF !important;
      font-size: 14px;
      font-weight: 600;
      padding: 5px 15px !important; }
      header ul.navbar-nav li > a:hover {
        opacity: 0.8; }
    header ul.navbar-nav li > a:hover {
